.App {
  text-align: center;
}

.react-monaco-editor-container {
  text-align: left;
}
#controls{
  text-align: right;
  -ms-flex-line-pack: right;
      align-content: right;
  display: inline;
  float: right; 
}
#EditorAndCanvas {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
}
#canvasLayers {
  position: relative;
  -ms-flex: 0px 1;
      flex: 0px 1
}
#turtleCanvas {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
}
#turtleOverlayCanvas {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
#gridCanvas {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}



.App-header {
  background-color: #222;
  text-align: left;
  /* height: 150px; */
  padding: 10px;
  color: white;
}
.App-header > h2 {
  display: inline;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



